            <b-td>920,- </b-td>
            <b-td>995,- </b-td>
            <b-td>1.075,- </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
      <h2>
        Welche Mglichkeiten habe ich nach der Ausbildung als Medizinische
        Fachangestellte?
      </h2>
      <p>
        Hast du deine Ausbildung erstmal in der Tasche, wird es auch als
        ausgelernt MFA garantiert nicht langweilig. Jeder kann sich entsprechend
        seiner Fhigkeiten und Interessen auf bestimmte Ttigkeiten
        spezialisieren, z. B. als
        <b-link to="/karriere/jobs-und-berufsbilder/verwaltung"
          >AbrechnungsmanagerIn</b-link
        >
        oder
        <b-link to="/karriere/jobs-und-berufsbilder/medizinisch-technisch"
          >MFA im ambulanten OP</b-link
        >. Manche mgen eher das Aufgabenfeld der AllrounderIn, um in allen
        Bereichen der Praxis eingesetzt zu sein.
      </p>
      <p>
        Das Wissen einer MFA ber Medizin, Therapien, Untersuchungen oder
        Abrechnung nimmt im Laufe der Berufsttigkeit stetig zu. So wirst du auf
        dem Arbeitsmarkt immer beliebt sein und keine Schwierigkeiten haben,
        eine Anstellung zu finden. Auch andere Arbeitgeber aus dem
        Gesundheitswesen schtzen die breiten Fhigkeiten von Medizinischen
        Fachangestellten. Sie versuchen auf sich aufmerksam zu machen durch
        attraktive Stellenangebote, die dir andere Mglichkeiten auerhalb der
        Arztpraxis bieten.
      </p>
      <p>
        Ganz allgemein haben wir die Kompetenzbereiche und Jobmglichkeiten fr
        Medizinische Fachangestellte / ArzthelferInnen in 5 Bereiche eingeteilt:
      </p>
      <b-row>
        <b-col
          v-for="card in cards"
          :key="card.title"
          class="my-3"
          cols="12"
          md="6"
          lg="4"
        >
          <b-card align="center icon-box shadow1 p-0">
            <b-card-body>
              <div class="d-flex justify-content-center">
                <div class="icon-box2 shadow1">
                  <Fa :icon="['fa', card.icon]" size="3x" />
                </div>
              </div>
              <b-card-title :title="card.title" class="bold" />
              <b-card-text>
                <p>
                  {{ card.desc }}
                </p>
              </b-card-text>
              <b-button
                :to="`/karriere/jobs-und-berufsbilder/${card.slug}`"
                variant="primary"
                >Mehr</b-button
              >
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
      <p>
        Jeder Bereich bietet verschiedene Mglichkeiten sich zu spezialisieren
        oder im Beruf und gehaltstechnisch aufzusteigen.
      </p>
      <p>
        Manche liebugeln eventuell mit dem Gedanken zu studieren. Hier gibt es
        einige Studiengnge, die mit entsprechender Berufserfahrung von MFAs
        absolviert werden knnen. Daneben gibt es verschiedene Fortbildungen
        oder Weiterbildungen im Sinne einer Aufstiegsfortbildung, die
        Medizinische Fachangestellte befhigen mehr Verantwortung zu bernehmen
        und in eine hhere Gehaltsklasse zu wechseln.
      </p>
      <p>
        Informationen ber die drei groen Bildungsformen, Fortbildung,
        Weiterbildung und Studium, und welche davon fr dich die Richtige ist,
        kannst du
        <b-link to="/blog/article/fortbildung-weiterbildung-oder-studium"
          >hier</b-link
        >
        nachlesen.
      </p>
      <p>
        In unserer Rubrik
        <b-link to="/karriere/fort-und-weiterbildungen"
          >Fort- und Weiterbildung</b-link
        >
        kannst du dich informieren, welche Mglichkeiten du als Medizinische
        Fachangestellte in und auerhalb der Arztpraxis hast. Krankenhuser,
        Medizinische Versorgungszentren, Rehakliniken, Gesundheitsmter,
        Medizinische Schreibbros, Pflegedienste, Krankenkassen, rztekammern
        oder Kassenrztliche Vereinigungen sind dabei einige Arbeitsorte fr
        Medizinische Fachangestellte.
      </p>
      <h2>
        Wieviel verdient man nach der Ausbildung zur Medizinischen
        Fachangestellten?
      </h2>
      <p>
        Das Gehalt einer MFA orientiert sich an dem Tarifvertrag fr
        Medizinische Fachangestellte. Ausfhrliche Informationen zum Gehalt fr
        Medizinische Fachangestellte nach der Ausbildung, den Ttigkeitsgruppen
        und Einteilung der Berufsjahre findest du
        <b-link to="/karriere/mfa/gehalt">hier</b-link>.
      </p>
      <p>
        Hast du noch Fragen zum Beruf oder der Ausbildung der Medizinischen
        Fachangestellten? Dann schreib uns doch einfach
        <b-link to="/kontakt">eine Nachricht</b-link>.
      </p>
      <h2 class="h4">Quellen</h2>
      <p>
        Alle Inhalte ber die Berufsausbildung zur/zum Medizinischen
        Fachangestellten kannst du in der entsprechenden
        <b-link
